<template>
  <div id="container">
    <div id="settings">
      <div class="user-pic">
        <img v-if="photo !== 'https://server.gold-ticket.uz/storage/app/'" :src="photo" alt="User Pic" @click.prevent="this.update()">
        <img v-else src="https://gold-ticket.uz/tgs/avatar.png" alt="User Pic" @click.prevent="this.update()">
      </div>
<!--      <div class="user-name">{{ this.first_name }} {{ this.last_name }}</div>-->
      <form class="settings-form" action="">
        <div class="form-control">
          <label class="form-label" for="name" v-if="lang === 'ru'">Имя</label>
          <label class="form-label" for="name" v-else>Isim</label>
          <input class="form-input" id="name" name="name" v-model="name" type="text">
        </div>
        <div class="form-control">
          <label class="form-label" for="phone" v-if="lang === 'ru'">Телефон</label>
          <label class="form-label" for="phone" v-else>Telefon</label>
          <input class="form-input" id="phone" name="phone" v-model="phone" disabled type="text">
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "Settings",
  data: () => ({
    photo: null,
    lang: null,
    name: null,
    phone: null,
  }),
  async mounted() {
    await axios.get('https://server.gold-ticket.uz/api/user/' + Telegram.WebApp.initDataUnsafe.user.id)
        .then(response => {
          this.photo = response.data.data.photo
          this.lang = response.data.data.lang
          this.name = response.data.data.name
          this.phone = response.data.data.phone
        })
        .catch(error => console.log(error));

    Telegram.WebApp.BackButton.show()
    Telegram.WebApp.BackButton.show()
        .onClick(() => {
      this.$router.push('/')
    });

    Telegram.WebApp.MainButton.show()
    if (this.user.local === 'ru') {
      Telegram.WebApp.MainButton.setParams({
        'text': "Сохранить",
        'color': "#f2c058",
        'text_color': "#FFF"
      })
    } else {
      Telegram.WebApp.MainButton.setParams({
        'text': "Saqlash",
        'color': "#f2c058",
        'text_color': "#FFF"
      })
    }
    Telegram.WebApp.MainButton.onClick(() => {
      Telegram.WebApp.MainButton.showProgress()
      this.submit()
      if (this.user.lang === 'ru') {
        Telegram.WebApp.showPopup({
          'title': 'Успешно',
          'message': 'Ваши данные успешно сохранены',
          'buttons': [
            {
              'id': 'success',
              'type': 'ok',
              'text': "Ок"
            }
          ]
        })
      } else {
        Telegram.WebApp.showPopup({
          'title': 'Muvofaqiyatli!',
          'message': 'Sizning malumotlaringiz muvofatqiyatli saqlandi',
          'buttons': [
            {
              'id': 'success',
              'type': 'ok',
              'text': "Ok"
            }
          ]
        })
      }
    })
  },
  methods: {
    async submit() {
      await axios
          .post('https://server.gold-tiket.uz/api/user/' + Telegram.WebApp.initDataUnsafe.user.id, {
            first_name: this.first_name,
            last_name: this.last_name
          })
          .then(response => {
            Telegram.WebApp.MainButton.hideProgress()
            this.$router.push('/')
          })
          .catch(error => {
            Telegram.WebApp.showPopup(error)
          })
    }
  }
}
</script>

<style scoped>
#container {
  display: flex;
  justify-content: center;
  align-items: center;
}

@media screen and (min-height: 734px) {
  #container {
    width: 100%;
    height: auto;
    padding: 0 35px;
    margin-bottom: 0;
    position: relative;
    top: 90px;
  }
}

@media screen and (min-height: 700px) {
  #container {
    width: 100%;
    height: auto;
    padding: 0 35px;
    margin-bottom: 0;
    position: relative;
    top: 90px;
  }
}

@media screen and (min-height: 498px) {
  #container {
    width: 100%;
    height: auto;
    padding: 0 35px;
    margin-bottom: 200px;
    position: relative;
    top: 80px;
  }
}
</style>
